import React from 'react';
import Styled from 'styled-components';
import Media from 'react-media';

const StyledHeader = Styled.header`
  width: 100%;
  font-family: Viga, sans-serif;
  color: #0B5DF5;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding-bottom: 70px;
  /* @media screen and (max-width: 768px) {
    position: relative;
  } */
`;

const StyledH1 = Styled.h1`
  margin: 0;
  font-size: 56px;
  line-height: 56px;
`;

const StyledNav = Styled.nav`
  display: block;
  @media screen and (max-width: 768px) {
    position: absolute;
    bottom: 20px;
    right: 20px;
  }
`;

const StyledUl = Styled.ul`
  margin: 0;
  padding: 0;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  font-size: 26px;
`;

const StyledLi = Styled.li`
  /* float: right; */
`;

const StyledLink = Styled.a`
  color: #0B5DF5;
  display: block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
`;

const Header = () => {
  return (
    <StyledHeader>
      <StyledH1>Marcus</StyledH1>
      <Media queries={{
        small: '(max-width: 767px)',
        large: '(min-width: 768px)',
      }}
      >
        {(matches) => (
          <>
            {matches.small && <p>I am small!</p>}
            {matches.large && 
              <StyledNav>
                <StyledUl>
                  <StyledLi><StyledLink href="#skills">Skills</StyledLink></StyledLi>
                  <StyledLi><StyledLink href="#experience">Experience</StyledLink></StyledLi>
                  <StyledLi><StyledLink href="#projects">Projects</StyledLink></StyledLi>
                </StyledUl>
              </StyledNav>
            }
          </>
        )}
      </Media>
    </StyledHeader>
  );
};

export default Header;
